.number-box-text {
  color: #777;
  position: absolute;
  top: 0.625rem;
  margin-right: 1.25rem;
}

.correct-answer {
  color: #094;
  font-size: 2.5rem;
  font-weight: bold;
  margin-right: 0.625rem;
}

.neutral-answer {
  color: #999;
  font-size: 2.5rem;
  font-weight: bold;
  margin-right: 0.625rem;
}

.wrong-answer {
  color: #FF0000;
  font-size: 2.5rem;
  font-weight: bold;
  margin-right: 0.625rem;
}

.nav-tabs {
  font-size: 13pt;
  text-align: center;
  font-weight: bold;
}

.nav-tabs > li.active, .nav-tabs > li.active:hover, .nav-tabs > li.active:focus {
  border-bottom: 0.0625rem solid #094;
  color: #222;
}

.your-result {
  font-family: "Noto Sans", sans-serif;
  font-weight: 700;
  color: #094;
  font-size: 1.875rem;
}

.result-label {
  font-size: 0.8125rem;
  margin-bottom: -0.3125rem;
  font-family: "Noto Sans", sans-serif;
}

.btn-group {
  font-size: 0.875rem;
  margin-bottom: 3.4375rem;
  margin-top: 0.875rem;
  font-family: "Noto Sans", sans-serif;
}

.btn-group > button.btn.btn-default {
  padding: 0.375rem 1.25rem;
}

.btn-group > .btn:first-child, .btn-group > .btn:last-child {
  -webkit-border-radius: 1.5625rem;
  -moz-border-radius: 1.5625rem;
}

.btn-default.active {
  color: #094;
}

.icon-space {
  width: 1.875rem;
}

.glyphicon.glyphicon-remove.small {
  margin-left: 0.3125rem;
}

/*univGameResultPage*/
.results-table {
  font-family: "Noto Sans", sans-serif;
  font-size: 0.75rem;
}

.results-table td {
  font-family: "Noto Sans", sans-serif;
  border-bottom: 0.0625rem solid #FFF;
  border-top: 0.0625rem solid #FFF;
}

.results-table tr {
  border-bottom: 0.0625rem solid #FFF;
  border-top: 0.0625rem solid #FFF;
}

.results-table td.sentence {
  font-style: italic;
  background-color: #eee;
}

.results-table td.fixed {
  width: 4.0625rem;
}

.results-table th {
  border-bottom: 0.0625rem solid #ddd;
  font-family: "Noto Sans", sans-serif;
  font-weight: 700;
}

.results-table .speed-number {
  font-family: "Noto Sans", sans-serif;
  font-weight: 700;
}

.table.bottom {
  margin-bottom: 0.3125rem;
}

td.total-nr {
  width: 2.5rem;
  font-family: "Noto Sans", sans-serif;
  font-weight: 700;
}

.clear-20 {
  margin-bottom: 1.25rem;
}

.clear-30 {
  margin-bottom: 1.875rem;
}

.clear-40 {
  margin-bottom: 2.5rem;
}

.clear-right {
  margin-right: 0.625rem;
}

.inner-block {
  margin: 0 auto;
  padding: 2rem 0rem 1.6rem 0rem;
}

.flexbox {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  margin: 0 auto;
  flex-wrap: nowrap;
}

.flexbox .row {
  padding: 8px 24px;
  margin: 0 !important;
  justify-content: center;
}

.flexbox h4:first-child {
  margin-right: 0.25rem;
}

.flexbox h4:last-child {
  margin-left: 0.25rem;
}


.btn-neutral-dark {
  border: 0px solid transparent;
  background: dodgerblue;
  color: white;
  border-radius: 6px;
  font-weight: 700;
  padding: 1rem 1.5rem 0.25rem;
}

.btn-neutral-dark:hover {
  background: #73b8fb;
  box-shadow: 0px 2px 10px 0px gray;
  -webkit-box-shadow: 0px 2px 10px 0px gray;
  -moz-box-shadow: 0px 2px 10px 0px gray;
}

.btn-neutral-dark:focus {
  background: #006dd8;
  box-shadow: 0px 2px 10px 0px;
  -webkit-box-shadow: 0px 2px 10px 0px;
  -moz-box-shadow: 0px 2px 10px 0px;
}

.btn-neutral-light {
  border: 0px solid transparent;
  background: #e9e9e9;
  color: #a9a9a9;
  border-radius: 6px;
  font-weight: 700;
  padding: 1rem 1.5rem 0.25rem;
}

.btn-neutral-light:hover {
  background: #f3f3f3;
  box-shadow: 0px 2px 10px 0px;
  -webkit-box-shadow: 0px 2px 10px 0px;
  -moz-box-shadow: 0px 2px 10px 0px;
}

.btn-neutral-dark:focus {
  background: #a9a9a9;
  color: #000000;
  box-shadow: 0px 2px 10px 0px;
  -webkit-box-shadow: 0px 2px 10px 0px;
  -moz-box-shadow: 0px 2px 10px 0px;
}

@media (min-width: 768px) {
  .howtoplay {
    background: url(../images/howtoplay.png) no-repeat center;
    background-size: 20.125rem;
    height: 11.75rem;
    margin-bottom: 1.25rem;
    display: block;
  }
  .inner-block {
    margin: 0 auto;
    padding: 2rem 2rem 1.6rem 2rem;
  }
  .flexbox h4 {
    font-size: 1rem;
  }
}

@media (min-width: 991px) {
  .flexbox h4 {
    font-size: 1.25rem;
  }
}

@media (min-width: 1200px) {
  .inner-block {
    max-width: 50rem;
  }

  .flexbox h4 {
    font-size: 1.5rem;
  }

}

